import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';

import CompareView from '../Templates/CompareView/CompareView';
import ChangeInitialFormData from '../Templates/ChangeInitialFormData/ChangeInitialFormData';
import HeaderDescWithButton from '../Templates/InsurerFormHeader/HeaderDescWithButton';
import Enquiry from '../Templates/Enquiry/Enquiry';

import PetEditDetail from './PetEditDetail';

import { ampTag } from '../../lib/tagFunctions';

import './PetChoosePlan.scss';

class PetChoosePlan extends Component {
  constructor(props) {
    super(props);

    this.doAmpTags = this.doAmpTags.bind(this);
  }

  doAmpTags(planTypeIndex = 0) {
    try {
      const { plansData } = this.props;
      const premium = plansData.plans.default.premium[planTypeIndex];
      ampTag('PetBuyNowA', {
        partner: plansData.selectedInsurers[0].insurerSlug,
        'plan-name': plansData.policyTypes[planTypeIndex],
        discount: `${((premium.price || 0) - (premium.net_price || 0)) / (premium.price || 0) * 100}%`,
        price: premium.net_price || 0,
      });
    } catch (error) {
      console.log(`Pet choose plan Buy now Amp tag failed: ${error.message}`);
    }
  }

  render() {
    const {
      plansData,
      petFields,
      submitCallback,
    } = this.props;

    try {
      for (let i = 0; i < plansData.plans.default.premium.length; ++i) {
        plansData.plans.default.premium[i].doTag = () => this.doAmpTags(i);
      }
    } catch (error) {
      console.log('Overwrite tags failed');
    }

    return (
      <Fragment>
        <Row className="inner-form-container" noGutters>
          <Col xs={12}>
            <ChangeInitialFormData
              form="pet"
              valuesToListen={[
                { value: 'pet_species', desc: 'Pet' },
                { value: 'start_date', desc: 'Start date' },
              ]}
              submitCallback={submitCallback}
            >
              <PetEditDetail isPopUp petFields={petFields} />
            </ChangeInitialFormData>
          </Col>
          <Col className="company-product-container" xs={12}>
            <HeaderDescWithButton
              logo={Logos.square[plansData.selectedInsurers[0].insurerSlug]}
              logoName={`${plansData.selectedInsurers[0].insurerSlug}.svg`}
              insurerName={plansData.selectedInsurers[0].insurerName}
              insurerDesc={plansData.policyDetails.about}
            />
            <Row className="content-container">
              <Col xs={12}>
                <p className="why">{`Why ${plansData.selectedInsurers[0].insurerName}?`}</p>
                <ul>
                  {
                    _.map(plansData.policyDetails.benefits, (benefit, index) => (
                      <li key={`benefit-${index}`}><p>{benefit}</p></li>
                    ))
                  }
                </ul>
              </Col>
              <CompareView
                category="pet"
                key="overview"
                className="overview-container-padding"
                data={plansData}
                currentTab="default"
                editForm={
                  <ChangeInitialFormData
                    form="pet"
                    valuesToListen={[
                      { value: 'brand', desc: 'Brand' },
                      { value: 'model', desc: 'Model' },
                      { value: 'year', desc: 'Year' },
                    ]}
                  >
                    <PetEditDetail isPopUp ref={node => this.editForm = node} />
                  </ChangeInitialFormData>
                }
              />
            </Row>
          </Col>
          <Enquiry />
        </Row>
      </Fragment>
    );
  }
}

PetChoosePlan.defaultProps = {
};

PetChoosePlan.propTypes = {
  plansData: PropTypes.object.isRequired,
  petFields: PropTypes.object.isRequired,
  submitCallback: PropTypes.func.isRequired,
};

export default PetChoosePlan;
