/**
 * PetMainContainer.js
 * The main container for pet
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import ReactPixel from 'react-facebook-pixel';

import PetMain from '../../components/Pet/PetMain';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { getPetFields, clearPetData } from '../../actions/petActions';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { gaTag, ampTag } from '../../lib/tagFunctions';

class PetMainContainer extends Component {
  constructor(props) {
    super(props);

    const {
      loading,
      petFields,
      history,
      clearData,
    } = this.props;

    clearData();

    if (!petFields) {
      loading(() => history.push('/'));
      this.props.getPetFields();
    }
  }

  componentDidMount() {
    const { setFormState, history } = this.props;
    setFormState(FORMSTATES.MAIN.index);

    window.scrollTo(0, 0);

    ReactPixel.track('CustomizeProduct');

    gaTag({
      page_title: 'Pet',
      page_path: `/pet${history.location.search}`,
    });
    ampTag('PetFormS');
  }

  componentDidUpdate(prevProps) {
    const { petFields, loadingEnd } = this.props;
    if (!prevProps.petFields && petFields) {
      loadingEnd();
    }
  }

  render() {
    const { petFields, petPromoGiveAway } = this.props;

    if (!petFields) {
      return null;
    }

    return (
      <PetMain
        petFields={petFields}
        petPromoGiveAway={petPromoGiveAway}
      />
    );
  }
}

PetMainContainer.defaultProps = {
  petFields: null,
  petPromoGiveAway: null,
};

PetMainContainer.propTypes = {
  petFields: PropTypes.object,
  getPetFields: PropTypes.func.isRequired,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  petPromoGiveAway: PropTypes.object,
  clearData: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  petFields: state.pet.petFields,
  petPromoGiveAway: state.insurance.promoGiveaways.pet,
});

const mapDispatchToProps = dispatch => ({
  getPetFields: () => {
    dispatch(getPetFields());
  },
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
  clearData: () => {
    dispatch(clearPetData());
  },
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PetMainContainer));
