/**
 * PetChoosePlanContainer.js
 * The choose plan container for pet
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';
import ReactPixel from 'react-facebook-pixel';

import PetChoosePlan from '../../components/Pet/PetChoosePlan';

import { setSelectedPlanData, getPetPlans, clearPetData } from '../../actions/petActions';
import { setInsuranceFormState, parseData, FORMSTATES } from '../../actions/insuranceActions';
import { setPlanType } from '../../actions/compareActions';
import { setRestrictionPopup } from '../../actions/restrictionsActions';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { gaTag, ampTag } from '../../lib/tagFunctions';

class PetChoosePlanContainer extends Component {
  static getDerivedStateFromProps(nextProps) {
    const {
      plansData,
      setFormState,
      loadingEnd,
      history,
    } = nextProps;

    if (plansData) {
      return {
        parsedData: parseData(plansData, null, null, null, () => {
          setFormState(FORMSTATES.DETAILFORM.index);
          history.push(`/pet${FORMSTATES.DETAILFORM.path}`);
        }, nextProps.setSelectedPlanData, loadingEnd, nextProps.setRestrictionPopup, 0),
      };
    }

    return null;
  }

  constructor(props) {
    super(props);

    const {
      plansData,
    } = this.props;

    this.state = {
      parsedData: null,
    };

    this.getNewData = this.getNewData.bind(this);

    if (!plansData) {
      this.getNewData();
    }
  }

  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.CHOOSEPLAN.index);
    ReactPixel.track('Search');
    gaTag({
      page_title: 'Pet - Choose Plan',
      page_path: '/pet/chooseplan',
    });
    ampTag('PetPlanS');
  }

  componentDidUpdate(prevProps) {
    const { plansData, loadingEnd } = this.props;
    if (!prevProps.plansData && plansData) {
      loadingEnd();
    }
  }

  getNewData() {
    const {
      petForm,
      loading,
      clearData,
      history,
    } = this.props;

    clearData();
    this.props.getPetPlans(petForm);
    loading(() => {
      clearData();
      history.goBack();
    }, 'No available plans found.', 30000);
  }

  render() {
    const { parsedData } = this.state;
    const { isLoggedIn, petFields } = this.props;

    if (!parsedData) {
      return null;
    }

    return (
      <PetChoosePlan
        petFields={petFields}
        plansData={parsedData}
        isFWD={false}
        isLoggedIn={isLoggedIn}
        submitCallback={this.getNewData}
      />
    );
  }
}

PetChoosePlanContainer.defaultProps = {
  plansData: null,
};

PetChoosePlanContainer.propTypes = {
  plansData: PropTypes.array,
  petForm: PropTypes.object.isRequired,
  petFields: PropTypes.object.isRequired,
  setFormState: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  getPetPlans: PropTypes.func.isRequired,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  clearData: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  petForm: getFormValues('pet')(state),
  plansData: state.pet.plansData,
  petFields: state.pet.petFields,
});

const mapDispatchToProps = dispatch => ({
  getPetPlans: petForm => (
    dispatch(getPetPlans(petForm))
  ),
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
  setPlanType: (category, type) => {
    dispatch(setPlanType(category, type));
  },
  setSelectedPlanData: (data) => {
    dispatch(setSelectedPlanData(data));
  },
  setRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
  clearData: () => {
    dispatch(clearPetData());
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PetChoosePlanContainer));
