import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';

import './HeaderDescWithButton.scss';

class HeaderDescWithButton extends Component {
  render() {
    const {
      logo,
      logoName,
      insurerName,
      insurerDesc,
      onClick,
    } = this.props;

    return (
      <Row className="header-desc-with-button-container" noGutters>
        <Col className="insurer-logo-container">
          <div className="company-logo-container">
            <img className="company-logo" src={logo} alt={logoName} />
          </div>
        </Col>
        <Col className="insurer-desc-container">
          <div className="insurer-desc-top">
            <span className="insurer-name">{insurerName}</span>
          </div>
          <p className="desc">
            {insurerDesc}
          </p>
        </Col>
        {
          onClick ?
            <div className="header-button-container">
              <Button variant="medium-flat-green" width={165} height={72} onClick={onClick}>
                <p>Continue</p>
              </Button>
            </div>
          :
            null
        }
      </Row>
    );
  }
}

HeaderDescWithButton.defaultProps = {
  onClick: null,
};

HeaderDescWithButton.propTypes = {
  logo: PropTypes.string.isRequired,
  logoName: PropTypes.string.isRequired,
  insurerName: PropTypes.string.isRequired,
  insurerDesc: PropTypes.string.isRequired,
  onClick: PropTypes.func,
};

export default HeaderDescWithButton;
