import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';
import PetDetail from '../../components/Pet/PetDetail';
import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { gaTag, gaEvent, ampTag } from '../../lib/tagFunctions';

class PetDetailContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.DETAILFORM.index);
    gaTag({
      page_title: 'Pet - Detail Form',
      page_path: '/pet/detailform',
    });
    ampTag('PetDetailsS');
  }

  render() {
    const {
      plansData,
      isLoggedIn,
      setFormState,
      profileValues,
      piggyCallback,
      petFields,
      history,
    } = this.props;

    if (!plansData) {
      return null;
    }

    const selectedData = plansData;

    let planDetails = {
      logo: Logos.others,
      logoDesc: '',
      planName: '',
      planTotal: null,
    };

    try {
      planDetails = {
        logo: Logos[selectedData.info.insurer_slug],
        logoDesc: `${selectedData.info.insurer_name}.svg`,
        planName: selectedData.info.product_name,
        planTotal: `$${selectedData.policyInfo[selectedData.planType].net_price.toFixed(2)}`,
      };
    } catch (error) {
      console.log('Get plan details failed');
    }

    return (
      <PetDetail
        petFields={petFields}
        selectedData={selectedData}
        planDetails={planDetails}
        isLoggedIn={isLoggedIn}
        piggyCallback={piggyCallback}
        changePlan={() => {
          history.goBack();
          gaEvent('Pet - DetailForm - ChangePlan', {
            event_category: 'Pet',
            event_label: 'Pet - DetailForm - ChangePlan',
          });
        }}
        profileValues={profileValues}
        submitCallback={() => {
          setFormState(FORMSTATES.CHECKOUT.index);
          history.push(`/pet${FORMSTATES.CHECKOUT.path}`);
        }}
      />
    );
  }
}

PetDetailContainer.defaultProps = {
};

PetDetailContainer.propTypes = {
  plansData: PropTypes.object.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  setFormState: PropTypes.func.isRequired,
  profileValues: PropTypes.object.isRequired,
  petFields: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};


const mapStateToProps = state => ({
  petFields: state.pet.petFields,
  profileValues: state.userDetails,
  plansData: state.pet.selectedPlanData,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PetDetailContainer));
