import _ from 'lodash';
import moment from 'moment';
// import { dateToString } from 'lib/dateFunctions';

const getInsurerLogo = (insurer_slug) => `https://webassets.policypal.com/insurer_logos/${insurer_slug}.png`;
const parseValues = (values) => {
  const keys = Object.keys(values);
  let result = {};

  for (let i = 0; i < keys.length; i++) {
    const key = keys[i];
    const item = values[key];
    
    switch(key) {
      case 'start_date':
      case 'end_date':
      case 'pet_birth_date':
        const date = _.isObject(item) ? item.value : item;
        result[key] = moment(date).format("DD/MM/YYYY");
        break;
      case 'trip_type':
      case 'plan_type':
      case 'num_children':
      case 'num_adult':
        result[key] = item.value;
        break;
      case 'region':
      case 'countries':
        result[key] = _.isArray(item.value) ? Array.from(item.value, c => c.value) : item.value.value;
        break;
      default:
        result[key] = item;
    }
  }

  return result;
}

export const getAddToCartData = (params = {}) => {
  const allowedKeys = [
    'insurer_slug',
    'insurer_name',
    'product_slug',
    'product_name',
    'coverage_level',
  ];

  let result = {};
  if (Object.keys(params).length > 0) {
    const {
      insurer_slug: insurer_slug = '',
      product_slug: product_slug = '',
      values:       values = {},
      price:        price = '',
    } = params;

    const product = {
      ..._.pick(params, allowedKeys),
      ...parseValues(values || {}),
      id: `${insurer_slug}__${product_slug}`,
      product_image: getInsurerLogo(insurer_slug),
      quantity: 1,
    }

    result = {
      content_ids:  [product.id],
      content_name: product.product_name,
      content_type: 'product',
      contents:     [product],
      ...((price) ? {currency: 'SGD', value: Number(price.replace('$', ''))} : {}),
    }
  }

  return result;
}