/**
 * PetCheckoutContainer.js
 * The checkout container for pet
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';
import ReactPixel from 'react-facebook-pixel';
import _ from 'lodash';

import { getAddToCartData } from 'lib/trackingHelper';
import { getSessionParams } from '../../actions/petActions';
import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import CheckoutWrapper from '../../components/Templates/Checkout/CheckoutWrapper';

import {
  compactCheckoutAttributes,
  getCheckoutParamsWording,
  getCheckoutHeaderDataCoverage,
  getCompactSubDetails,
} from '../../lib/helperFunctions';
import { gaTag, gaEvent, ampTag } from '../../lib/tagFunctions';

class PetCheckoutContainer extends Component {
  constructor(props) {
    super(props);

    const {
      petForm,
      petDetailsForm,
      selectedPlanData,
      isAdHoc,
      adHocData,
    } = this.props;

    const paramsWording = getCheckoutParamsWording(
      isAdHoc,
      adHocData,
      selectedPlanData,
      () => getSessionParams(petForm, petDetailsForm, selectedPlanData),
    );

    const headerCoverage = getCheckoutHeaderDataCoverage('Pet Insurance', isAdHoc, adHocData, selectedPlanData, paramsWording.params);

    const petIllness = isAdHoc ? adHocData.data.session_params.pet_illness : petDetailsForm.pet_illness;
    const medicalRecord = isAdHoc ? adHocData.data.session_params.pet_medical_record : petDetailsForm.pet_medical_record;
    const record = petIllness === 'Yes' ?
      [{ title: 'Illness Details', description: medicalRecord }] :
      [];

    const petFormToUse = isAdHoc ? adHocData.data.session_params : paramsWording.params.sessionParams.session_params;
    const petDetailFormToUse = isAdHoc ? adHocData.data.session_params : paramsWording.params.sessionParams.session_params;
    this.state = {
      headerData: headerCoverage.headerData,
      mainDetails: {
        title: 'Insurance',
        attributes: compactCheckoutAttributes([
          { title: 'Pet Species', description: petFormToUse.pet_species },
          { title: 'Start Date', description: petFormToUse.start_date },
          { title: 'End Date', description: petFormToUse.end_date },
          // { title: 'Coverage', description: headerCoverage.coverage },
        ]),
      },
      subDetails: [getCompactSubDetails({
        key: 'Adult 1',
        leftDetail: {
          title: 'Details',
          attributes: compactCheckoutAttributes([
            { title: 'Email', description: petDetailFormToUse.email },
            { title: 'Last name', description: petDetailFormToUse.family_name },
            { title: 'First name', description: petDetailFormToUse.given_name },
            { title: 'Date of birth', description: petDetailFormToUse.birth_date },
            { title: 'ID type', description: petDetailFormToUse.id_type },
            { title: 'ID number', description: petDetailFormToUse.nric },
            { title: 'Mobile number', description: petDetailFormToUse.mobile_number },
            { title: 'Address', description: petDetailFormToUse.address },
            { title: 'Unit no', description: petDetailFormToUse.unit_no },
            { title: 'Postcode', description: petDetailFormToUse.postal },
          ]),
        },
        rightDetail: {
          title: 'Pet',
          attributes: compactCheckoutAttributes([
            { title: 'Name', description: petDetailFormToUse.pet_name },
            { title: 'Microchip number', description: petDetailFormToUse.pet_microchip_no },
            { title: 'Date of birth', description: petDetailFormToUse.pet_birth_date },
            { title: 'Pet Illness', description: petDetailFormToUse.pet_illness },
            ...record,
          ]),
        },
      })],
      ...paramsWording,
    };
  }

  componentDidMount() {
    const { setFormState, history } = this.props;
    setFormState(FORMSTATES.CHECKOUT.index);
    gaTag({
      page_title: 'Pet - Checkout',
      page_path: '/pet/checkout',
    });
    ampTag('PetCheckoutS');

    if (history.location.pathname && history.location.pathname === `/pet${FORMSTATES.CHECKOUT.path}`) {
      const { petDetailsForm, petForm, selectedPlanData } = this.props;
      const { params } = this.state;
      const { info, planType } = selectedPlanData;
      const trackData = getAddToCartData({
        ..._.pick(info, ['insurer_slug', 'insurer_name', 'product_slug', 'product_name']),
        values: {
          ..._.pick(petDetailsForm, ['pet_name', 'pet_birth_date', 'pet_illness', 'pet_microchip_no', 'pet_medical_record']),
          ...petForm,
        },
        coverage_level: planType,
        price: `$${params.amountPayable}`,
      });
      ReactPixel.track('AddToCart', trackData);
      ReactPixel.track('InitiateCheckout');
    }
  }

  render() {
    const {
      headerData,
      mainDetails,
      subDetails,
      wording,
      params,
    } = this.state;

    const {
      isLoggedIn,
      charged,
      setFormState,
      history,
      isAdHoc,
      adHocData,
      sessionId,
    } = this.props;

    return (
      <CheckoutWrapper
        headerData={headerData}
        mainDetails={mainDetails}
        subDetails={subDetails}
        sessionId={sessionId}
        isLoggedIn={isLoggedIn}
        charged={charged}
        backToDetails={() => {
          history.goBack();
        }}
        wording={wording}
        isAdHoc={isAdHoc}
        adHocData={adHocData}
        params={params}
        category="pet"
        trackingTags={{
          payment: (data) => {
            ReactPixel.track('AddPaymentInfo');
            gaEvent('Pet - CheckoutPage_ProceedPayment', {
              event_category: 'Pet',
              event_label: 'Pet - CheckoutPage_ProceedPayment',
            });
            ampTag('PetProceedPayA', {
              cashback: data.rewardsID !== null,
              promo: data.promoCode !== null && data.promoCode !== '',
            });
          },
          receipt: () => {
            if (!isAdHoc) {
              ReactPixel.track('Purchase', { currency: 'SGD', value: params.amountPayable });
              setFormState(FORMSTATES.SUBMITTED.index);
              history.push(`/pet${FORMSTATES.SUBMITTED.path}`);
            }
            gaTag({
              page_title: 'Pet - Receipt',
              page_path: '/pet/Receipt',
            });
            ampTag('PetConfirmationS');
          },
        }}
      />
    );
  }
}

PetCheckoutContainer.defaultProps = {
  charged: null,
  sessionId: null,
  adHocData: null,
  isAdHoc: false,
  isLoggedIn: false,
  petForm: null,
  petDetailsForm: null,
  selectedPlanData: null,
};

PetCheckoutContainer.propTypes = {
  charged: PropTypes.bool,
  sessionId: PropTypes.string,
  isLoggedIn: PropTypes.bool,
  petForm: PropTypes.object,
  petDetailsForm: PropTypes.object,
  selectedPlanData: PropTypes.object,
  adHocData: PropTypes.object,
  isAdHoc: PropTypes.bool,
  setFormState: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  const sessionId = ownProps.isAdHoc ? ownProps.sessionId : state.insurance.sessionId;
  return ({
    petForm: getFormValues('pet')(state),
    petDetailsForm: getFormValues('pet-detail')(state),
    selectedPlanData: state.pet.selectedPlanData,
    sessionId,
  });
};

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PetCheckoutContainer));
