/**
 * PetRouteContainer.js
 * The wrapper container for pet
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { change, reset } from 'redux-form';
import { /* Prompt, */ withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import { clearPetData } from '../../actions/petActions';
import { getPromoGiveaways, resetInsuranceForm, FORMSTATES } from '../../actions/insuranceActions';
import { resetCompare } from '../../actions/compareActions';
import { isProduction } from '../../lib/tagFunctions';

import StepProgressBar from '../../components/Templates/StepProgressBar/StepProgressBar';
import RefreshTimer from '../../components/Templates/RefreshTimer/RefreshTimer';

import PetMainContainer from './PetMainContainer';
import PetChoosePlanContainer from './PetChoosePlanContainer';
import PetDetailContainer from './PetDetailContainer';
import PetCheckoutContainer from './PetCheckoutContainer';

import { dateToStoreString } from '../../lib/dateFunctions';
import { routeHandler } from '../../lib/helperFunctions';

const formName = 'pet';
const detailFormName = 'pet-detail';

class PetRouteContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { formIndex, isMounted } = prevState;
    const { formState, match, history } = nextProps;

    if (isMounted) {
      const routeResult = routeHandler(formName, formState, formIndex, match, history);
      if (routeResult && routeResult.formIndex !== null) {
        return ({
          formIndex: routeResult.formIndex,
        });
      }
    }
    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      formIndex: 0,
      isInitialLoad: true,
    };

    const {
      resetStoreForm,
      clearData,
      petPromoGiveAway,
      history,
    } = this.props;

    history.replace(`/pet${history.location.search}`);

    if (petPromoGiveAway === undefined) {
      this.props.getPromoGiveaways();
    }

    clearData();
    this.props.resetInsuranceForm();
    this.props.resetCompare();

    resetStoreForm('pet');
    resetStoreForm('pet-detail');

    this.setInitialFormState = this.setInitialFormState.bind(this);
    this.renderStepProgress = this.renderStepProgress.bind(this);
    this.renderPetForms = this.renderPetForms.bind(this);
    this.changeUserDetails = this.changeUserDetails.bind(this);
  }

  componentDidMount() {
    // window.onbeforeunload = () => true;
    this.setInitialFormState();
    this.setState({
      // eslint-disable-next-line
      isMounted: true,
    });
  }

  componentDidUpdate(prevProps) {
    const { isInitialLoad } = this.state;
    const { userDetails } = this.props;

    if (isInitialLoad && _.isEqual(userDetails, prevProps.userDetails)) {
      this.changeUserDetails();
    }
  }

  componentWillUnmount() {
    const { clearData, resetStoreForm } = this.props;
    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('pet');
    resetStoreForm('pet-detail');
  }

  setInitialFormState() {
    const { changeForm } = this.props;

    const startDate = new Date();
    startDate.setDate(startDate.getDate() + 1);
    changeForm(formName, 'start_date', dateToStoreString(startDate));

    this.changeUserDetails();
  }

  changeUserDetails() {
    const { isInitialLoad } = this.state;
    const { userDetails, changeForm } = this.props;

    if (userDetails) {
      changeForm(detailFormName, 'email', userDetails.email);
      changeForm(detailFormName, 'given_name', userDetails.firstName);
      changeForm(detailFormName, 'family_name', userDetails.surname);
      changeForm(detailFormName, 'id_type', userDetails.id_type);
      changeForm(detailFormName, 'nric', userDetails.nric);
      changeForm(detailFormName, 'mobile_number', userDetails.contact);
      changeForm(detailFormName, 'address', userDetails.address);
      changeForm(detailFormName, 'unit_no', userDetails.unit_no);
      changeForm(detailFormName, 'postal', userDetails.postal_code);

      if (isInitialLoad) {
        this.setState({
          isInitialLoad: false,
        });
      }
    }
  }

  renderStepProgress() {
    const { formIndex } = this.state;
    const { charged } = this.props;

    return (
      <StepProgressBar
        maxWidth="556px"
        formState={formIndex}
        inputList={[
          'Choose Plan',
          'Details',
          'Checkout',
        ]}
        form="pet"
        stateList={[
          FORMSTATES.CHOOSEPLAN,
          FORMSTATES.DETAILFORM,
          FORMSTATES.CHECKOUT,
        ]}
        stateDescriptions={[
          { main: '' /* 'Choose your preferred plan' */},
          { main: 'Details' },
          { main: 'Checkout' },
        ]}
        charged={charged}
      />
    );
  }

  renderPetForms() {
    const { formIndex } = this.state;
    const {
      userDetails,
      charged,
    } = this.props;

    const isLoggedIn = !_.isEmpty(userDetails);

    switch (formIndex) {
      case FORMSTATES.CHOOSEPLAN.index:
        return <PetChoosePlanContainer isLoggedIn={isLoggedIn} />;
      case FORMSTATES.DETAILFORM.index:
        return (
          <PetDetailContainer
            isLoggedIn={isLoggedIn}
            piggyCallback={this.changeUserDetails}
          />
        );
      case FORMSTATES.CHECKOUT.index:
      case FORMSTATES.SUBMITTED.index:
        return <PetCheckoutContainer isLoggedIn={isLoggedIn} charged={charged} />;
      default:
        return <PetMainContainer isLoggedIn={isLoggedIn} />;
    }
  }

  render() {
    const { petPromoGiveAway, constants } = this.props;

    if (!constants || petPromoGiveAway === undefined) {
      return null;
    }

    const meta = isProduction ? 'PolicyPal, pet insurance, dog insurance, cat insurance, pet insurance singapore, dog insurance singapore, cat insurance singapore, PetCare' : '';
    return (
      <Fragment>
        <Helmet>
          <meta name="keywords" content={meta} />
          <title>PolicyPal 2.0 - Pet Insurance</title>
          <meta name="title" content="PolicyPal 2.0 - Pet Insurance" />
          <meta name="description" content="Browse products for pet insurance." />
        </Helmet>
        {/* {
          formState !== FORMSTATES.MAIN ?
            <Prompt
              when={true}
              message="Are you sure you want to leave current page?"
            />
          :
            null
        } */}
        {this.renderStepProgress()}
        {this.renderPetForms()}
        <RefreshTimer ref={(node) => {
            if (!this.refresher) {
              node.invokeNewTimer();
            }
            this.refresher = node;
          }}
        />
      </Fragment>
    );
  }
}

PetRouteContainer.defaultProps = {
  userDetails: null,
  charged: null,
  constants: null,
  petPromoGiveAway: undefined,
};

PetRouteContainer.propTypes = {
  clearData: PropTypes.func.isRequired,
  resetCompare: PropTypes.func.isRequired,
  resetInsuranceForm: PropTypes.func.isRequired,
  changeForm: PropTypes.func.isRequired,
  resetStoreForm: PropTypes.func.isRequired,
  userDetails: PropTypes.object,
  charged: PropTypes.bool,
  constants: PropTypes.object,
  getPromoGiveaways: PropTypes.func.isRequired,
  petPromoGiveAway: PropTypes.object,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  formState: state.insurance.formState,
  constants: state.insurance.constants,
  charged: state.insurance.charged,
  userDetails: state.userDetails.data,
  petPromoGiveAway: state.insurance.promoGiveaways.pet,
});

const mapDispatchToProps = dispatch => ({
  changeForm: (form, key, value) => (
    dispatch(change(form, key, value))
  ),
  resetInsuranceForm: () => (
    dispatch(resetInsuranceForm())
  ),
  resetStoreForm: form => (
    dispatch(reset(form))
  ),
  resetCompare: () => {
    dispatch(resetCompare());
  },
  clearData: () => {
    dispatch(clearPetData());
  },
  getPromoGiveaways: () => {
    dispatch(getPromoGiveaways('pet'));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PetRouteContainer));
