import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import Pet1 from 'assets/images/Pet/policypal-pet-insurance-1.svg';
import Pet2 from 'assets/images/Pet/policypal-pet-insurance-2.svg';
import Pet3 from 'assets/images/Pet/policypal-pet-insurance-3.svg';

import PetEditDetail from './PetEditDetail';
import FormLanding from '../Templates/FormLanding/FormLanding';

class PetMain extends Component {
  render() {
    const { petFields, petPromoGiveAway } = this.props;
    return (
      <div className="pet-container">
        <FormLanding
          img1={Pet1}
          img2={Pet2}
          img3={Pet3}
          imgAlt="PolicyPal Pet Insurance"
          promo={petPromoGiveAway}
          desc={
            <Fragment>
              <p>Get 5% Cashback </p>
              <p>upon purchase of Pet Insurance!</p>
            </Fragment>
          }
        >
          <PetEditDetail
            petFields={petFields}
          />
        </FormLanding>
      </div>
    );
  }
}

PetMain.defaultProps = {
  petPromoGiveAway: null,
};

PetMain.propTypes = {
  petFields: PropTypes.object.isRequired,
  petPromoGiveAway: PropTypes.object,
};

export default PetMain;
